/* You can add global styles to this file, and also import other style files */
@use '@angular/material' as mat;
@use '@angular/material-experimental' as matx;



@import './styles/layout.scss';
@import './styles/complain.scss';
@import 'swiper/scss';
@import 'swiper/scss/effect-coverflow';
@import 'swiper/scss/navigation';
@import 'swiper/scss/pagination';
@import './styles/material.scss';
@import './styles/editor.scss';

@font-face {
	font-family: 'NotoSans';
	font-style: normal;
	font-weight: 400;
	src: url(./assets/font/NotoSansKR-Regular.ttf) format('opentype');
}

@font-face {
	font-family: 'Pretendard-Regular';
	src: url('https://fastly.jsdelivr.net/gh/Project-Noonnu/noonfonts_2107@1.1/Pretendard-Regular.woff') format('woff');
	font-weight: 400;
	font-style: normal;
}

/* fallback */



$baseColor : #227b4f;

$my-typography: mat.define-typography-config(
	$font-family:'Pretendard-Regular'
);
@include mat.typography-hierarchy($my-typography);

//   3 
$m3-light-theme: matx.define-theme((
  color: (
    primary: matx.$m3-azure-palette,
    tertiary: matx.$m3-blue-palette,
    ),
  typography: (
	plain-family: 'Pretendard-Regular',
	brand-family: 'Pretendard-Regular'
  )
));

//   pre-built themes 
$my-theme: mat.define-light-theme((
	typography: mat.define-typography-config(
		$font-family: 'Pretendard-Regular'
	),
));
@include mat.all-component-themes($my-theme);

* {
	box-sizing: border-box;
}


html, body {
	width: 100%;
	height: 100%;
	font-family: 'Pretendard-Regular', "NotoSans" !important;
  padding: 0;
	margin: 0;
	// font-family: "NotoSans" !important;
	@include mat.all-component-themes($m3-light-theme);
}


body {
	color: #222;
	font-family: "NotoSans";
	font-size: 1rem;
	font-weight: 400;
	line-height: 1.4;
	word-break: keep-all;
	word-wrap: break-word;
	background-color: white;
}

dl, ul, ol, li {
	list-style: none;
}
.pointer{
	cursor: pointer;
}

button, input, textarea, select {
	font-family: "NotoSans";
	font-size: 1rem;
	border: none;
	cursor: pointer;
	display: inline-block;
}

button {
	background-color: transparent;
}

fieldset, form, h1, h2, h3, h4, h5, h6, html, img {
	border: 0;
}

pre {
	font-family: "NotoSans";

}

.chat-box-answer {
	display: inline-block;
	padding: 15px 20px;
	background-color: #edf4f2;
	border-top-right-radius: 20px;
	border-bottom-left-radius: 20px;
	border-bottom-right-radius: 20px;
	margin: 4px 0 10px;
}

.chat-box-question {
	display: inline-block;
	color:#fff;
	padding: 15px 20px;
	background-color: #348F6C;
	border-top-left-radius: 20px;
	border-bottom-left-radius: 20px;
	border-bottom-right-radius: 20px;
	margin: 10px 0;
}

