//raised-button
.mat-mdc-raised-button {
	background-color: #{$baseColor} !important;
	color: white !important;
}

.mat-mdc-raised-button.mat-mdc-button-disabled {
	color: white !important;
	background-color: rgb(107, 107, 107)  !important;
}

//
.mat-mdc-tab-group, .mat-mdc-tab-nav-bar {
    --mdc-tab-indicator-active-indicator-color: #227b4f ;
    --mat-tab-header-disabled-ripple-color: rgba(0, 0, 0, 0.38);
    --mat-tab-header-pagination-icon-color: black;
    --mat-tab-header-inactive-label-text-color: rgba(0, 0, 0, 0.6);
    --mat-tab-header-active-label-text-color: #227b4f ;
    --mat-tab-header-active-ripple-color: #227b4f ;
    --mat-tab-header-inactive-ripple-color: #227b4f ;
    --mat-tab-header-inactive-focus-label-text-color: rgba(0, 0, 0, 0.6);
    --mat-tab-header-inactive-hover-label-text-color: rgba(0, 0, 0, 0.6);
    --mat-tab-header-active-focus-label-text-color: #227b4f ;
    --mat-tab-header-active-hover-label-text-color: #227b4f ;
    --mat-tab-header-active-focus-indicator-color: #227b4f ;
    --mat-tab-header-active-hover-indicator-color: #227b4f ;
}

//
.mat-mdc-standard-chip.mat-mdc-chip-selected.mat-primary, .mat-mdc-standard-chip.mat-mdc-chip-highlighted.mat-primary {
    --mdc-chip-elevated-selected-container-color: #227b4f;
    --mdc-chip-elevated-container-color: #227b4f;
}

//
html .mat-mdc-slide-toggle.mat-accent {
    --mdc-switch-selected-focus-state-layer-color: #65b28c;
    --mdc-switch-selected-handle-color: #65b28c;
    --mdc-switch-selected-hover-state-layer-color: #65b28c;
    --mdc-switch-selected-pressed-state-layer-color: #65b28c;

    --mdc-switch-selected-focus-handle-color: #227b4f;
    --mdc-switch-selected-hover-handle-color: #227b4f;
    --mdc-switch-selected-pressed-handle-color: #227b4f;

    --mdc-switch-selected-focus-track-color: #afd667;
    --mdc-switch-selected-hover-track-color: #afd667;
    --mdc-switch-selected-pressed-track-color: #afd667;
    --mdc-switch-selected-track-color: #afd667;
}

//
.mat-step-icon-selected {
    background-color: #227b4f !important;
}


//
.mat-primary {
    --mat-full-pseudo-checkbox-selected-icon-color: #227b4f;
    --mat-minimal-pseudo-checkbox-selected-checkmark-color: #227b4f;
}

.mat-mdc-option.mdc-list-item--selected:not(.mdc-list-item--disabled) .mdc-list-item__primary-text {
    color: #227b4f !important;
}

.mdc-text-field--filled:not(.mdc-text-field--disabled).mdc-text-field--focused .mdc-floating-label, .mdc-text-field--filled:not(.mdc-text-field--disabled).mdc-text-field--focused .mdc-floating-label--float-above {
    color: #227b4f !important;
}

.mat-mdc-form-field.mat-focused .mat-mdc-select-arrow {
    color: #227b4f !important;
}

.mdc-text-field--filled:not(.mdc-text-field--disabled) .mdc-line-ripple::after {
    border-bottom-color: #227b4f !important;
}

.title {
    color: #000;
    font-size: 2rem !important;
    font-weight: 700 !important;
    letter-spacing: -1px !important;
    line-height: 180% !important;
}

.text-ellipsis {
	overflow: hidden; /*    */
	white-space: nowrap; /*     */
	text-overflow: ellipsis; /*   */
	display: block;
  }
