.full-layout {
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  margin: 0;
  z-index: 1;
}

.splitter {
  align-self: center;
  width: 24px;
  border: none;
  border-left: 2px solid #cdcdcd;
  padding: 0;
  box-sizing: border-box;
  cursor: ew-resize;

  position: relative;
}

.splitter-button-layout {
  align-self: center; 
  margin: 0 0 10rem 0;
}

.splitter-button {
  display: block !important;
  position: absolute !important;
  z-index: 1 !important;
}

.global-menu {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 1.5rem 1.2rem;
}